<template>
  <div class="filout-body">
    <div class="filout-box">
      <div>
        <p>2.其他信息填写法人</p>
        <p>
          <input v-model="qi" type="radio" name="per" value="1" />法人
          <input v-model="qi" type="radio" name="per" value="0" />代理人
        </p>
      </div>
      <div v-if="qi === '1'">
        <p>*法人认证:法人身份信息认证，请上传法人身份证件(正反面)</p>
        <p class="positive">
          <Upload
            class="upload"
            @onChange="onPersonjust"
            :scid="Personjust"
            :limit="1"
          />
          <Upload
            class="upload"
            @onChange="onPerson"
            :scid="Person"
            :limit="1"
          />
        </p>
        <p class="positive"><span>正面</span> <span>反面</span></p>
      </div>
      <div v-if="qi === '0'">
        <!--  <p>*法人认证:法人身份信息认证，请上传法人身份证件(正反面)</p>
        <p>
          <Upload class="upload" @onChange="onPersonjust" /><Upload
            class="upload"
            @onChange="onPerson"
          />
        </p> -->
        <p>
          *授权信息:上传加盖公章的原件照片或扫描件，上传大小不超过5M。下载授权模板
        </p>
        <p><Upload @onChange="onPower" :scid="Power" :limit="2" /></p>
        <p>*权人认证︰授权人身份信息认证，请上传授权人身份证件(正反面)</p>
        <p class="positive">
          <Upload
            class="upload"
            @onChange="onGierjust"
            :scid="Gierjust"
            :limit="1"
          />
          <Upload class="upload" @onChange="onGier" :scid="Gier" :limit="1" />
        </p>
        <p class="positive"><span>正面</span> <span>反面</span></p>
      </div>
      <p>
        <span> *入驻类型选择︰</span>
        <span v-for="(item, index) in list" :key="index">
          <input
            type="radio"
            name="sex"
            @click="select(item.id, item.className)"
          />
          {{ item.className }}
        </span>
      </p>
      <p>
        <input type="checkbox" name="nema" @click="check" />
        &nbsp;
        <span>我同意</span>
        <a href="/agreement">《入驻协议》</a>
      </p>
      <button @click="sub">提交</button>
    </div>
  </div>
</template>

<script>
import Upload from "@/components/upload.vue";
export default {
  components: { Upload },
  data() {
    return {
      qi: "1",
      trademarkType: [],
      classId: "",
      checked: false,
      list: [],
      Personjust: [],
      Person: [],
      Gierjust: [],
      Gier: [],
      Power: [],
      classitem: "",
      che: 0,
    };
  },
  async mounted() {
       this.trademarkType = JSON.parse(this.$route.query.str);
    let { data } = await this.$http.classlist();
    console.log(data);
    this.list = [...data.list];
  },
  methods: {
    select(id, item) {
      this.classId = id;
      this.classitem = item;
      console.log(item);
    },
    agre() {
      this.$router.push({
        path: "/agreement",
      });
    },
    check() {
      this.checked = event.target.checked;
    },
    sub() {
      if (
        this.Personjust.length > 0 &&
        this.Person.length > 0 &&
        this.checked != false &&
        this.classId != ""
      ) {
        this.merInfo();
        return;
      } else {
        this.$message("必填项为空");
      }
      if (
        this.Gierjust.length > 0 &&
        this.Gier.length > 0 &&
        this.checked != false &&
        this.Power.length > 0 &&
        this.classId != ""
      ) {
        this.merInfo();
        return;
      } else {
        this.$message("必填项为空");
      }
    },
    async merInfo() {
      let str = {
        classId: this.classId,
        isLegal: this.qi,
        merId: sessionStorage.getItem("id"),
        trademarkType: this.trademarkType,
        businessOperation: this.classitem,
      };
      let { data } = await this.$http.addmerInfo(str);
      console.log(data);
      if (data.code == "0000") {
          window.open(data.data.url)
          this.$router({
            path:'/home'
          })
      }
      if (data.code == "2001") {
        this.$message(data.message);
      }
    },
    async onPersonjust(fileList) {
      //法人身份证正面
      let from = new FormData();
      from.append("file", fileList);
      from.append("merId", sessionStorage.getItem("id"));
      from.append("picType", 2);
      let { data } = await this.$http.meruoload(from);
      console.log(data);
      if (data.code == "0000") {
        this.Personjust.push(data.data.merPicInfo.id);
      }
    },
    async onPerson(fileList) {
      //法人身份证反面
      let from = new FormData();
      from.append("file", fileList);
      from.append("merId", sessionStorage.getItem("id"));
      from.append("picType", 3);
      let { data } = await this.$http.meruoload(from);
      console.log(data);
      if (data.code == "0000") {
        this.Person.push(data.data.merPicInfo.id);
      }
    },
    async onPower(fileList) {
      //授权信息
      let from = new FormData();
      from.append("file", fileList);
      from.append("merId", sessionStorage.getItem("id"));
      from.append("picType", 6);
      let { data } = await this.$http.meruoload(from);
      console.log(data);
      if (data.code == "0000") {
        this.Power.push(data.data.merPicInfo.id);
      }
    },
    async onGierjust(fileList) {
      //授权人身份证正面
      let from = new FormData();
      from.append("file", fileList);
      from.append("merId", sessionStorage.getItem("id"));
      from.append("picType", 2);
      let { data } = await this.$http.meruoload(from);
      console.log(data);
      if (data.code == "0000") {
        this.Gierjust.push(data.data.merPicInfo.id);
      }
    },
    async onGier(fileList) {
      //授权人身份证反面
      let from = new FormData();
      from.append("file", fileList);
      from.append("merId", sessionStorage.getItem("id"));
      from.append("picType", 3);
      let { data } = await this.$http.meruoload(from);
      console.log(data);
      if (data.code == "0000") {
        this.Gier.push(data.data.merPicInfo.id);
      }
    },
  },
};
</script>

<style scoped>
.filout-body {
  width: 100vw;
  height: 100vh;
  background-image: url("/bg.jpg");
  display: flex;
  align-items: center;
}
.filout-box {
  width: 40%;
  background: #fff;
  margin: 0 auto;
  padding: 30px;
}
.filout-box button {
  width: 200px;
  height: 30px;
  margin-left: 240px;
}
.upload {
  display: inline;
  margin-right: 30px;
}
.positive {
  width: 100%;
  display: flex;
  justify-content: space-around;
}
.positive span {
  width: 50%;
  padding-right: 30px;
  display: inline-block;
  text-align: center;
}
</style>